import {useEffect, useState} from 'react';
import CalendarSingle from "../CalendarSingle/CalendarSingle";
import DateHistory from "../DateHistory/DateHistory";
import DateSearch from "../DateSearch/DateSearch";
import DateTimeUntil from "../DateTimeUntil/DateTimeUntil";
import TopTenBillboards from "../TopTenBillboards/TopTenBillboards";
import Card from "../Card/Card";
import "./Home.css";
import utils from "../../utils";

const days = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];

const months = [
    "January", "February", "March", "April", "May", "June", "July",
    "August", "September", "October", "November", "December"
];

const Home = ({selected,setSelected}) => {

    useEffect(()=>{
        let monthPartial = utils.months[selected.getMonth()].toLowerCase().slice(0, 3);
        let stateObj = { id: "100" };

        window.location.host.split(":")[0] === "localhost" || window.location.host.split(":")[0] === "DateTools" ?
            window.history.pushState(stateObj,
                "DateTools", `/home/${selected.getFullYear()}/${monthPartial}/${selected.getDate()}`) :
            window.history.pushState(stateObj,
                "DateTools", `/DateTools/home/${selected.getFullYear()}/${monthPartial}/${selected.getDate()}`)

      },[selected])

    return (
        <div className="home">
            <div className="home-header row">Use the date picker and other date tools to learn more about what happened on the selected day.</div>
            <div className="row">
                <DateSearch selected={selected} setSelected={setSelected}/>
                <div className="day-selected-title">
                    Showing date information for <br></br>{days[selected.getDay()]} {months[selected.getMonth()]} {selected.getDate()}, {selected.getFullYear()}
                </div>
            </div>
            <div className="row">
                <div className="col-sm">
                    <CalendarSingle selected={selected} setSelected={setSelected}/>
                </div>
                <div className="col-sm">
                    <DateTimeUntil selected={selected}/>
                </div>
            </div>
            <div className="row center-content">
                        <TopTenBillboards selected={selected}/>        
            </div>
            <div className="row center-content">
                <DateHistory 
                    selected={selected}
                    month={selected.getMonth()+1}
                    year={selected.getFullYear()}
                    day={selected.getDate()}
                />
            </div>
        </div>
    );
};
  
export default Home;