import {useState,useEffect} from 'react';
import Tabs from "../Tabs/Tabs";
import Card from "../Card/Card";
import CardSections from "../CardSections/CardSections";
import Events from "../DateHistoryEvents/DateHistoryEvents";
import axios from 'axios';
import cheerio from 'cheerio';
import utils from '../../utils';
import "./DateHistory.css";

const {months} = utils;

const tabs = [
    "Events",
    "Births",
    "Deaths",
    "Holidays and observances"
];

async function scrapeSite(url,setTabData,storeSiteData){
    const html = await axios.get(url);
    const $ = await cheerio.load(html.data.parse.text);
    let data = {};

    $('h2').each((i, elem) => {
        if (i > 0 && i <= 4) {
            data[$(elem).text().trim().replace('[edit]','')]=[];
        }
    });

    //console.log(data);

    $('ul').each((i, elem) => {
        if(elem.prev&&elem.prev.prev){
            const elemHeader = $(elem.prev.prev).text().trim().replace('[edit]','')
            if (tabs.includes(elemHeader)) {
                // console.log($(elem.prev.prev).text().trim())
                //console.log($(elem).find("li"));
                $(elem).children().each((i, child) =>{
                    //  console.log(child
                    //         .children
                    //         .filter(c=>[c.data,c.title])
                    //         .map(e=>{
                    //             if(e.name==="a")
                    //                 return <a href={`wikipedia.org${e.attribs.href}`}>{$(e).text()}</a>
                    //             else return $(e).text();
                    //         })
                    //     );
                    data[elemHeader].push({
                        text:child
                            .children
                            .filter(c=>[c.data,c.title])
                            .map(e=>{
                                if(e.name==="a")
                                    return <a key={`https://wikipedia.org${e.attribs.href}`} href={`https://wikipedia.org${e.attribs.href}`}>{$(e).text()}</a>
                                else return $(e).text();
                            }),

                        subText:child
                            .children
                            .filter(c=>c.name==="ul").length?
                            child
                                .children
                                .filter(c=>c.name==="ul")[0]
                                .children
                                .filter(node=>node.name)
                                .map(e=>{
                                    if(e.name==="a")
                                        return <a key={`https://wikipedia.org${e.attribs.href}`} href={`https://wikipedia.org${e.attribs.href}`}>{$(e).text()}</a>
                                    else return $(e).text();
                                })
                            :[]
                    })
                });
            }
        }
    });

   setTabData(data);
   storeSiteData(url,data);
}

const DateHistory = ({month,day}) => {
    const [activeTab,setActiveTab] = useState("Events");
    const [activeTabComponent,setActiveTabComponent] = useState();
    const [events,setEvents] = useState([]);
    const [births,setBirths] = useState([]);
    const [deaths,setDeaths] = useState([]);
    const [holidays,setHolidays] = useState([]);

    const setTabData=(data)=>{
        setEvents(data["Events"]);
        setBirths(data["Births"]);
        setDeaths(data["Deaths"]);
        setHolidays(data["Holidays and observances"]);
    }

    // 
    const storeSiteData=(url,data)=>{
        //sessionStorage.setItem(url,JSON.stringify(data));
    }

    useEffect(()=>{
        const url = `https://en.wikipedia.org/w/api.php?origin=*&action=parse&page=${months[month-1]}_${day}&prop=text&formatversion=2&format=json`;
        sessionStorage.getItem(url)?
        setTabData(JSON.parse(sessionStorage.getItem(url))):
        scrapeSite(url,setTabData,storeSiteData);
    },[day, month]);

    useEffect(()=>{
        if(!events.length) return;
        
        setActiveTabComponent();
    },[events, activeTab, births, deaths, holidays]);

    const dataSets = [births,deaths,holidays,events];

    return (
        <Card
            border={true}
            title={`On ${months[month-1]} ${day} in history`}
            content={
                <CardSections 
                    headers={tabs} 
                    content={dataSets.map(set=><Events events={set}/>)}/>}
        />
    );
};
  
export default DateHistory;